@import './global/global';

// Custom elements need a `display` property before things like background colors will work. Who knew!?
cu-testimonial-deck {
  display: flex;
  flex-direction: column;
  position: relative;
  container-type: inline-size;

  .cu-testimonial__content {
    text-align: left;
  }

  .cu-testimonial:has(.cu-testimonial__media) > .cu-testimonial__inner::after {
    justify-self: start;
  }
}

@container (min-width: 500px) {

  cu-testimonial-deck .cu-testimonial__inner,
  cu-testimonial-deck .cu-testimonial__media {
    height: 100%;
  }
  cu-testimonial-deck .cu-testimonial__media {
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      inset: 0 0 0 0;
      background-image: var(--cu-testimonial-deck-media-url);
      background-position: center;
      background-size: cover;
      filter: blur(50px);
    }
  }
  cu-testimonial-deck .cu-testimonial__media img {
    position: relative;
    height: unset;
  }

}

.cu-testimonial-deck__item-wrapper {
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  width: 100%;
}

.cu-testimonial-deck__items {
  display: flex;
  gap: var(--cu-ps1);
}

.cu-testimonial-deck__item {
  width: 100cqw;
  scroll-snap-align: start;
  flex-shrink: 0;
  overflow: hidden;
  transition: opacity .5s;
}

cu-testimonial-deck[data-state="paused"] .cu-testimonial-deck__playpause {
  .pause {
    display: none;
  }
}

cu-testimonial-deck[data-state="playing"] .cu-testimonial-deck__playpause {
  .play {
    display: none;
  }
}

.cu-testimonial-deck__thumbnails {
  display: flex;
  gap: 5px;
  margin: 0;
  padding: 0 10px;
  width: 100%;
  height: 32px;
  position: absolute;
}

.cu-testimonial-deck__thumbnail {
  display: block;
  flex-grow: 1;
  height: 100%;
  padding-top: 12px;
  position: relative;
  cursor: pointer;

  &::after {
    content: '';
    display: block;
    width: 0;
    height: 5px;
    background-color: white;
    visibility: hidden;
    position: absolute;
    border-radius: 2.5px;
  }

  &.active::after {
    width: 100%;
    visibility: visible;
  }

  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 5px;
    position: absolute;
    background-color: rgba(217,217,217,.5);
    backdrop-filter: blur(2px);
    border-radius: 2.5px;
  }
}

cu-testimonial-deck[data-state="playing"] .cu-testimonial-deck__thumbnail::after {
  transition: width var(--cu-testimonials-duration, 5s) linear;
}

@container (min-width: 500px) {
  .cu-testimonial-deck__thumbnails {
    // @see .cu-testimonial:has(.cu-testimonial__media) .cu-testimonial__inner { grid-template-columns }
    width: 40.3cqw;
  }
}

.cu-testimonial-deck__nav {
  display: flex;
  gap: 5px;
  position: absolute;
  top: 86cqw;
  right: 0;
  backdrop-filter: blur(20px); // Obscures the red bar behind the buttons.
}

@container (min-width: 500px) {
  .cu-testimonial-deck__nav {
    justify-content: center;
    gap: 2px;
    top: unset;
    right: unset;
    bottom: 0;
    width: 40.3cqw;
    backdrop-filter: unset;
  }
}

.cu-testimonial-deck__nav-button {
  width: 42px;
  height: 42px;
  padding: 0;
  color: white;
  background-color: var(--cu-color-brand);
  border: 2px solid var(--cu-colorscheme-accent);
  cursor: pointer;

  svg {
    width: 100%;
    height: 100%;
  }
}
